<script>
import axios  from "axios";
 //
  //
 


export default {
   name:'login',
   data(){
     return{
       username:'',
       password:'',
     }
   },
   methods: {
     register(){

       var readyData=this.$qs.stringify({
         username:this.username,
          password:this.password,
       })
      axios.post('http://localhost:5005/api/register',readyData).then(res=>{
        console.log(res)
      }).catch(err=>{
        console.log(err)
      })

     },
     login(){
      var readyData1=this.$qs.stringify({
           username:this.username,
           password:this.password,
       })
      axios.post('http://localhost:5005/api/login',readyData1).then(res=>{
        window.sessionStorage.setItem('token', res.data.data.token);
        this.$router.push('/home')
      }).catch(err=>{
        console.log(err)
      })
      
   },

   },
   
}
</script>

